___CSS_LOADER_EXPORT___.push([module.id, `/* About section style*/

#content h1{
    color: white;
    font-family: var(--primary-font);
    font-size: calc(1rem + 2vw);
    width: 50vw;
    margin: max(10px, 3vw);
}
/* 
#about_grid
{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    grid-auto-rows: minmax(200px, auto);
    gap: 17px;
}

.grid-item
{
    width: minmax(300px, 30vw);
    background-color: aqua; 
    display: flex;
    align-items: center;
    justify-content: center;
} */
 
.about p
{
    font-family: var(--secondary-font);
    font-size: calc(1.2rem + 0.4vw);
    width: 50vw;
    color: white;
}
.about
{
    display: flex;
    gap: 13px;
}

.about img{
    width: 30%;
    height: auto;
    box-shadow: 30px 30px 40px rgba(0,0,0,.7);
    border-radius: 70%/ 37% 2% 8% 20%;
}

.contact
{
    height: 70vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.13);
  
}
.contact h1
{
    margin: 0;

}

form
{
    width: 100%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    padding: 7% 5%;
    justify-content: center;
    align-items: center;
    justify-self: center;
    align-self: center;
    font-size: calc(1rem + 0.3vh);
    gap: 5px;
    
}
form > input[type="submit"]
{
    width: max-content;
    flex-grow: 0;
    margin: 30px;
    font-size: calc(10px + 0.9vh);
    
}

form > p
{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

#formSubmissionButton
{
    background-color: var(--btn-theme);
    padding: calc(0.5rem + 0.1vh) calc(1.5rem + 0.5vh);
    border: none;
    border-radius: 3px;
    font-family: var(--secondary-font), sans-serif;
    color: white;
    font-size: calc(0.75rem + 0.5vw);
    
}
#formSubmissionButton:hover
{
    opacity: 0.7;
    transition: all 0.3s;
}`, "",{"version":3,"sources":["webpack://./src/about.css"],"names":[],"mappings":"AAAA,uBAAuB;;AAEvB;IACI,YAAY;IACZ,gCAAgC;IAChC,2BAA2B;IAC3B,WAAW;IACX,sBAAsB;AAC1B;AACA;;;;;;;;;;;;;;;;GAgBG;;AAEH;;IAEI,kCAAkC;IAClC,+BAA+B;IAC/B,WAAW;IACX,YAAY;AAChB;AACA;;IAEI,aAAa;IACb,SAAS;AACb;;AAEA;IACI,UAAU;IACV,YAAY;IACZ,yCAAyC;IACzC,iCAAiC;AACrC;;AAEA;;IAEI,YAAY;IACZ,YAAY;IACZ,qCAAqC;;AAEzC;AACA;;IAEI,SAAS;;AAEb;;AAEA;;IAEI,WAAW;IACX,sBAAsB;IACtB,aAAa;IACb,sBAAsB;IACtB,cAAc;IACd,uBAAuB;IACvB,mBAAmB;IACnB,oBAAoB;IACpB,kBAAkB;IAClB,6BAA6B;IAC7B,QAAQ;;AAEZ;AACA;;IAEI,kBAAkB;IAClB,YAAY;IACZ,YAAY;IACZ,6BAA6B;;AAEjC;;AAEA;;IAEI,WAAW;IACX,aAAa;IACb,8BAA8B;AAClC;;AAEA;;IAEI,kCAAkC;IAClC,kDAAkD;IAClD,YAAY;IACZ,kBAAkB;IAClB,8CAA8C;IAC9C,YAAY;IACZ,gCAAgC;;AAEpC;AACA;;IAEI,YAAY;IACZ,oBAAoB;AACxB","sourcesContent":["/* About section style*/\r\n\r\n#content h1{\r\n    color: white;\r\n    font-family: var(--primary-font);\r\n    font-size: calc(1rem + 2vw);\r\n    width: 50vw;\r\n    margin: max(10px, 3vw);\r\n}\r\n/* \r\n#about_grid\r\n{\r\n    display: grid;\r\n    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));\r\n    grid-auto-rows: minmax(200px, auto);\r\n    gap: 17px;\r\n}\r\n\r\n.grid-item\r\n{\r\n    width: minmax(300px, 30vw);\r\n    background-color: aqua; \r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n} */\r\n \r\n.about p\r\n{\r\n    font-family: var(--secondary-font);\r\n    font-size: calc(1.2rem + 0.4vw);\r\n    width: 50vw;\r\n    color: white;\r\n}\r\n.about\r\n{\r\n    display: flex;\r\n    gap: 13px;\r\n}\r\n\r\n.about img{\r\n    width: 30%;\r\n    height: auto;\r\n    box-shadow: 30px 30px 40px rgba(0,0,0,.7);\r\n    border-radius: 70%/ 37% 2% 8% 20%;\r\n}\r\n\r\n.contact\r\n{\r\n    height: 70vh;\r\n    width: 100vw;\r\n    background-color: rgba(0, 0, 0, 0.13);\r\n  \r\n}\r\n.contact h1\r\n{\r\n    margin: 0;\r\n\r\n}\r\n\r\nform\r\n{\r\n    width: 100%;\r\n    box-sizing: border-box;\r\n    display: flex;\r\n    